	module.exports = "<h2 id=overview>Overview</h2> <p>The dropdown menu can contain either links or buttons, depending on the expected behavior when clicking the menu item. If you are using the menu item to navigate to another page, then menu item is a link. Otherwise, use a button for the menu item.</p> <h2 id=accessibility>Accessibility</h2> <table> <thead> <tr> <th>Attribute</th> <th>Applied</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>aria-haspopup=&quot;true&quot;</code></td> <td><code>.pf-c-dropdown__toggle</code></td> <td>Indicates the button has a popup menu.</td> </tr> <tr> <td><code>aria-expanded=&quot;false&quot;</code></td> <td><code>.pf-c-dropdown__toggle</code></td> <td>Indicates that the menu is hidden</td> </tr> <tr> <td><code>aria-expanded=&quot;true&quot;</code></td> <td><code>.pf-c-dropdown__toggle</code></td> <td>Indicates that the menu is visible</td> </tr> <tr> <td><code>aria-label=&quot;Actions&quot;</code></td> <td><code>.pf-c-dropdown__toggle</code></td> <td>Provides an accessible name for the dropdown when an icon is used instead of text. <strong>Required when icon is used with no supporting text</strong></td> </tr> <tr> <td><code>role=&quot;menu&quot;</code></td> <td><code>.pf-c-dropdown__menu</code></td> <td>Indicates that the menu is a menu</td> </tr> <tr> <td><code>hidden=&quot;hidden&quot;</code></td> <td><code>.pf-c-dropdown__menu</code></td> <td>Indicates that the menu is hidden so that it isn&#39;t visible in the UI and isn&#39;t accessed by assistive technologies</td> </tr> <tr> <td><code>aria-expanded=&quot;true&quot;</code></td> <td><code>.pf-c-dropdown__menu</code></td> <td>Indicates that the menu is visible</td> </tr> <tr> <td><code>role=&quot;menuitem&quot;</code></td> <td><code>.pf-c-dropdown__menu-item</code></td> <td>Indicates that the menu item is a menu item</td> </tr> <tr> <td><code>role=&quot;separator&quot;</code></td> <td><code>.pf-c-dropdown__separator</code></td> <td>Indicates that the separator is a separator</td> </tr> <tr> <td><code>disabled</code></td> <td><code>button.pf-c-dropdown__menu-item</code></td> <td>When the menu item uses a button element, indicates that it is unavailable and removes it from keyboard focus</td> </tr> <tr> <td><code>aria-disabled=&quot;true&quot;</code></td> <td><code>a.pf-c-dropdown__menu-item</code></td> <td>When the menu item uses a link element, indicates that it is unavailable</td> </tr> <tr> <td><code>tabindex=&quot;-1&quot;</code></td> <td><code>a.pf-c-dropdown__menu-item</code></td> <td>When the menu item uses a link element, removes it from keyboard focus</td> </tr> </tbody> </table> <h2 id=usage>Usage</h2> <table> <thead> <tr> <th>Class</th> <th>Applied</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>.pf-c-dropdown</code></td> <td><code>&lt;div&gt;</code></td> <td>Defines the parent wrapper of the dropdown.</td> </tr> <tr> <td><code>.pf-c-dropdown__toggle</code></td> <td><code>&lt;button&gt;</code></td> <td>Defines the dropdown toggle</td> </tr> <tr> <td><code>.pf-c-dropdown__toggle-icon</code></td> <td><code>&lt;i&gt;</code></td> <td>Defines the dropdown toggle icon</td> </tr> <tr> <td><code>.pf-c-dropdown__menu</code></td> <td><code>&lt;div&gt;</code></td> <td>Defines the parent wrapper of the menu items</td> </tr> <tr> <td><code>.pf-c-dropdown__menu-item</code></td> <td><code>&lt;a&gt;</code></td> <td>Defines a menu item that navigates to another page</td> </tr> <tr> <td><code>.pf-c-dropdown__menu-item</code></td> <td><code>&lt;button&gt;</code></td> <td>Defines a menu item that performs an action on the current page</td> </tr> <tr> <td><code>.pf-c-dropdown__separator</code></td> <td><code>&lt;div&gt;</code></td> <td>Defines a separator within the menu</td> </tr> <tr> <td><code>.pf-m-expanded</code></td> <td><code>.pf-c-dropdown</code></td> <td>Modifies for the expanded state</td> </tr> <tr> <td><code>.pf-m-plain</code></td> <td><code>.pf-c-dropdown</code></td> <td>Modifies to display the toggle with no border or background</td> </tr> <tr> <td><code>.pf-m-top</code></td> <td><code>.pf-c-dropdown</code></td> <td>Modifies to display the menu above the toggle</td> </tr> <tr> <td><code>.pf-m-align-right</code></td> <td><code>.pf-c-dropdown</code></td> <td>Modifies to display the menu aligned to the right edge of the toggle</td> </tr> <tr> <td><code>.pf-m-hover</code></td> <td><code>.pf-c-dropdown__menu-item</code>, <code>.pf-c-dropdown__toggle</code></td> <td>Forces display of the hover state of the element. This state is primarily for demonstration purposes and would not normally be used in lieu of the <code>:hover</code> pseudo-class.</td> </tr> <tr> <td><code>.pf-m-focus</code></td> <td><code>.pf-c-dropdown__menu-item</code></td> <td>Forces display of the focus state of the element. This state is primarily for demonstration purposes and would not normally be used in lieu of the <code>:focus</code> pseudo-class.</td> </tr> <tr> <td><code>.pf-m-active</code></td> <td><code>.pf-c-dropdown__toggle</code></td> <td>Forces display of the active state of the element. This state is primarily for demonstration purposes and would not normally be used in lieu of the <code>:active</code> pseudo-class.</td> </tr> <tr> <td><code>.pf-m-disabled</code></td> <td><code>a.pf-c-dropdown__menu-item</code></td> <td>Modifies to display the menu item as disabled. This applies to <code>a.pf-c-dropdown__menu-item</code> and should not be used in lieu of the <code>:disabled</code> attribute on <code>button.pf-c-dropdown__menu-item</code></td> </tr> </tbody> </table> ";

